<template>
  <div class="main-container">
    <div class="section-header">
      <img src="../src/assets/images/photo-png/logo.png" alt="Logo">
      <TimeComponent v-show="screenWidth > '960'"/>
      <WeatherComponent v-show="screenWidth > '960'"/>
      <div class="d-flex justify-space-around align-center">
        <v-icon v-if="isLoginChecked === true && showLoginStatus === 'login'" style="margin: 0 5px 0 5px"
                :style="loginStatus">mdi-account-check
        </v-icon>
        <v-icon v-if="isLoginChecked === true && showLoginStatus === 'blocked'" style="margin: 0 5px 0 5px; color: red">
          mdi-account-remove
        </v-icon>
        <v-btn
            id="menu-activator"
            color="primary"
            size="small"
            :disabled="showLoginStatus === 'blocked'"
        >
          <span v-if="screenWidth < '961'"
                style="display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 3px"><v-icon>mdi-menu</v-icon>Меню</span>
          <span v-if="screenWidth > '960'">Навигация по сайту</span>
        </v-btn>
        <v-menu activator="#menu-activator">
          <v-list
              style="margin-top: 5px;"
          >
            <v-list-item
                v-for="(item, index) in menuItems"
                :key="index"
                :value="index"
                @click="chooseOperation(item.title)"
                :style="{color: item.color}"
            >
              <v-list-item-title>
                <v-icon :icon="item.icon" style="margin: 0 3px 0 0" :style="{color: item.color}"></v-icon>
                {{ item.title }}
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
    </div>
    <div class="section-body">
      <v-progress-circular
          :size="70"
          width="7"
          color="blue-darken-2"
          indeterminate
          v-if="isLoginChecked === false"
      ></v-progress-circular>
      <router-view v-if="isLoginChecked === true"></router-view>
    </div>
    <div class="section-asside">
      <div class="section-asside__title">PRODUCT`S CARD & DASHBOARD</div>
      <div class="section-asside__logo">
        <img src="./assets/images/photo-png/logo.png" alt="">
      </div>
    </div>
    <div class="section-footer">
      © TNP Sales <sup>2.0</sup> 2023 - {{ footerYear }}
    </div>
  </div>
  <div class="text-center">
    <v-dialog
        v-model="dialog"
        max-width="400"
        persistent
    >

      <v-card
          prepend-icon="mdi-information"
          :text="dialogText"
          :title="dialogTitle"
      >
        <template v-slot:actions>
          <v-spacer></v-spacer>

          <v-btn @click="logOut" color="success">
            Выйти
          </v-btn>

          <v-btn @click="dialog = false" color="red">
            Отмена
          </v-btn>
        </template>
      </v-card>
    </v-dialog>
  </div>
  <div class="text-center">
    <v-snackbar
        v-model="snackbar"
        multi-line
        timeout="4000"
        style="margin-bottom: 60px"
    >
      {{ snackbarText }}

      <template v-slot:actions>
        <v-btn
            color="red"
            variant="text"
            @click="snackbar = false"
        >
          Закрыть
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import TimeComponent from "@/components/header/TimeComponent.vue";
import WeatherComponent from "@/components/header/WeatherComponent.vue";
import $ from "jquery";

export default {
  name: 'App',
  components: {WeatherComponent, TimeComponent},
  data() {
    return {
      isLoginChecked: false,
      footerYear: '',
      loginStatus: {},
      showLoginStatus: 'blocked',
      menuItems: [
        {title: 'Главная', icon: 'mdi-home', color: ''},
        {title: 'Аналитика', icon: 'mdi-chart-line', color: ''},
        {title: 'Справочник', icon: 'mdi-book-open-page-variant', color: ''},
        {title: 'План-Факт', icon: 'mdi-table', color: ''},
        {title: 'Отпуска', icon: 'mdi-calendar', color: ''},
        {title: 'Расчёты', icon: 'mdi-calculator', color: ''},
        {title: 'Выйти', icon: 'mdi-logout-variant', color: 'red'},
      ],
      screenWidth: window.innerWidth,
      empoyersAuthData: null,
      dialog: false,
      dialogText: '',
      dialogTitle: '',
      snackbar: false,
      snackbarText: '',
    }
  }, created() {
    window.addEventListener('resize', this.updateHeight);
  },
  methods: {
    async checkAuth() {
      let isAuthLocal = JSON.parse(localStorage.getItem("sale-auth"));

      let checkLoadLocal = localStorage.getItem('checkLoadStatus');
      if (checkLoadLocal) {
        localStorage.removeItem('checkLoadStatus');
      }

      if (isAuthLocal === null) {
        setTimeout(() => {
          this.isLoginChecked = true;
          this.showLoginStatus = 'blocked';
          this.$router.push('login')
        }, 1000)
      } else if (isAuthLocal !== null && isAuthLocal.role === 'manager') {
        await $.ajax({
          url: 'https://script.google.com/macros/s/AKfycbzlA3mS-tvOR0onR5y3seF1E8p7CwsUPuhxhKFGVKOf-btPNTqLGOv3V-vtg9c4LKJT5A/exec',
          type: "GET",
          crossDomain: true,
          dataType: "json",
          success: (response) => {
            let isActualEmployer = response.stuff.filter(item => item.LoginName == isAuthLocal.login);
            if (isActualEmployer.length > 0) {
              setTimeout(() => {
                this.isLoginChecked = true;
                this.getLoginItemColor(isAuthLocal.role);
                this.$router.push('/')
              }, 1000)
            } else if (isActualEmployer.length < 1) {
              setTimeout(() => {
                localStorage.removeItem('sale-auth');
                this.isLoginChecked = true;
                this.showLoginStatus = 'blocked';
                this.$router.push('login')
              }, 1000)
            }
          },
          error: (error) => {
            this.snackbarText = 'Ошибка загрузки данных с вервера, перезагрузите страницу'
            this.snackbar = true;
            console.log(error);
          }
        });
      } else if (isAuthLocal !== null && isAuthLocal.role === 'promoter') {
        await $.ajax({
          url: 'https://script.google.com/macros/s/AKfycbzlA3mS-tvOR0onR5y3seF1E8p7CwsUPuhxhKFGVKOf-btPNTqLGOv3V-vtg9c4LKJT5A/exec',
          type: "GET",
          crossDomain: true,
          dataType: "json",
          success: (response) => {
            let isActualEmployer = response.stuff.filter(item => item.LoginName == isAuthLocal.login);
            if (isActualEmployer.length > 0) {
              setTimeout(() => {
                this.isLoginChecked = true;
                this.getLoginItemColor(isAuthLocal.role);
                this.$router.push('/')
              }, 1000)
            } else if (isActualEmployer.length < 1) {
              setTimeout(() => {
                sessionStorage.removeItem('sale-auth');
                this.isLoginChecked = true;
                this.showLoginStatus = 'blocked';
                this.$router.push('login')
              }, 1000)
            }
          },
          error: (error) => {
            this.snackbarText = 'Ошибка загрузки данных с вервера, перезагрузите страницу'
            this.snackbar = true;
            console.log(error);
          }
        });
      } else if (isAuthLocal !== null && isAuthLocal.role === 'super') {
        setTimeout(() => {
          this.isLoginChecked = true;
          this.getLoginItemColor(isAuthLocal.role);
          this.$router.push('/')
        }, 1000)
      }
    },
    footerData() {
      let date = new Date();
      this.footerYear = date.toLocaleDateString().slice(6);
    },
    chooseOperation(operation) {
      let isOperate = localStorage.getItem('checkLoadStatus');
      if (isOperate === 'loaded') {
        if (operation === 'Выйти') {
          this.dialogTitle = 'Выход';
          this.dialogText = 'Вы уверены, что хотите выйти?';
          this.dialog = true;
        } else if (operation === 'Аналитика') {
          this.$router.push('error')
        } else if (operation === 'Главная') {
          this.$router.push('/')
        } else {
          console.log(operation);
        }
      } else {
        this.snackbarText = 'Дождитесь выполнения операции'
        this.snackbar = true;
      }
    },
    logOut() {
      this.isLoginChecked = false;
      this.dialog = false
      localStorage.removeItem("sale-auth");
      window.location.reload();
    },
    updateHeight() {
      this.screenWidth = window.innerWidth;
    },
    getLoginItemColor(loginRole) {
      if (loginRole === 'super') {
        this.loginStatus = {margin: '0 5px 0 5px', color: 'green'};
      } else if (loginRole === 'manager') {
        this.loginStatus = {margin: '0 5px 0 5px', color: 'orange'};
      } else if (loginRole === 'promoter') {
        this.loginStatus = {margin: '0 5px 0 5px', color: 'dodgerblue'};
      }
      this.showLoginStatus = 'login';
    },
  },
  mounted() {
    this.footerData();
    this.checkAuth();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.updateHeight);
  }
}
</script>

