<template>
  <div class="weather-container">
    <div class="weather">
      <div class="weather__city" :class="{'weather-error': weatherIsUnload}">{{ weatherCity }}</div>
      <img class="weather__img" :src="weatherImg" alt="">
      <div class="weather__temperature" :style="{color: weatherTempColor}">{{ weatherTemp }}</div>
    </div>

    <div class="weather-currency-divider"></div>

    <div class="currency">
      <div class="currency-logo">
        <v-icon icon="mdi-currency-usd"></v-icon>
      </div>
      <div class="currency-info" :class="{'currency-error': currencyIsUnload}">{{ currencyInfo }}</div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "WeatherComponent",
  data() {
    return {
      weatherCity: '',
      weatherTemp: '',
      weatherImg: '',
      weatherTempColor: '',
      currencyInfo: '',

      weatherIsUnload: false,
      currencyIsUnload: false,
    }
  },
  methods: {
    showWeather() {
      $.ajax({
        url: "https://api.weatherapi.com/v1/current.json?key=5f6ae58e18e644888bf182855242904&q=Днепропетровск",
        method: "get",
        crossDomain: true,
        dataType: "json",
        success: (data) => {
          let location = data.location.name;
          let temperature = data.current.temp_c;
          let icon = data.current.condition.icon;

          this.weatherCity = location.slice(0, 5);
          this.weatherTemp = `${temperature}°C`;
          if (parseFloat(temperature) >= 35) {
            this.weatherTempColor = 'orangered';
          } else if (parseFloat(temperature) < 0) {
            this.weatherTempColor = 'dodgerblue';
          }
          this.weatherImg = icon;
        },
        error: () => {
          this.weatherIsUnload = true;
          this.weatherCity = 'ERROR';
        },
      });
    },
    showCurrency() {
      $.ajax({
        url: "https://bank.gov.ua/NBUStatService/v1/statdirectory/exchange?json",
        method: "GET",
        crossDomain: true,
        dataType: "json",
        success: (data) => {
          let usd = data.filter(f => f.txt == 'Долар США');
          let resultUsdInfo = usd[0].rate;
          this.currencyInfo = resultUsdInfo;
        },
        error: () => {
          this.currencyIsUnload = true;
          this.currencyInfo = 'ERROR';
        },
      });
    },
  },
  mounted() {
    this.showWeather();
    this.showCurrency();

    setInterval(() => {
      this.showWeather();
      this.showCurrency();
    }, 1800000);
  }
}
</script>

<style scoped>

.weather-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  .weather {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    gap: 5px;
    font-weight: bold;
    color: gray;

    .weather-error{
      color: red;
    }

    img {
      max-width: 50px;
      max-height: 50px;
      margin: 0 3px 0 5px;
    }
  }

  .weather-currency-divider {
    width: 1px;
    background-color: gray;
    height: 20px;
    margin: 0 7px 0 15px;
  }

  .currency {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-weight: bold;
    color: green;
    position: relative;

    &::after {
      content: 'НБУ';
      position: absolute;
      top: -7px;
      right: -25px;
      font-size: 9px;
      letter-spacing: 1px;
      color: gray;
    }

    .currency-error{
      color: red;
    }
  }
}

</style>